<template>
    <span class="deadline-text">{{ currentDeadline.days + " : " + currentDeadline.hours + " : " + currentDeadline.minutes}}</span>
</template>

<script>
    export default {
        name: "ListItemTimer",
        props: {
            deadlinePropP: Object,
        },
        data: function(){
            return {
                currentDeadline: this.deadlinePropP,
            }
        },
        created: function(){
            let time = new Date();
            const monthDurations = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
            this.timerGoing(monthDurations, time, {month: this.currentDeadline.month, day: this.currentDeadline.day, hours: this.currentDeadline.hour, minutes: this.currentDeadline.minute})
        },
        methods: {
            deadlineClock(first ,destroyFlag){
                console.log(this.currentDeadline);
                let timeOut;
                if(!first)this.currentDeadline.minutes = this.currentDeadline.minutes - 1;
                if(this.currentDeadline.minutes < 0 && this.currentDeadline.hours > 0){
                    this.currentDeadline.minutes = 59;
                    this.currentDeadline.hours = this.currentDeadline.hours - 1;
                }
                if(this.currentDeadline.hours < 0 && this.currentDeadline.days > 0){
                    this.currentDeadline.hours = 23;
                    this.currentDeadline.days = this.currentDeadline.days - 1;
                }
                if((this.currentDeadline.days !== 0 || this.currentDeadline.hours !== 0 || this.currentDeadline.minutes !== 0) && destroyFlag){
                    timeOut = setTimeout(() => this.deadlineClock(false, this.currentDeadline.license), 60000)
                }else{
                    clearTimeout(timeOut)
                }
            },
            timerGoing: function (array, nowTime, deadline) {
                let timeForDeadline = {
                    days: 0,
                    hours: 0,
                    minutes: 0,
                    license: true
                }
                timeForDeadline.minutes = timeForDeadline.minutes + (deadline.minutes + deadline.hours * 60) - (nowTime.getMinutes() + ( nowTime.getHours() * 60 ))
                timeForDeadline.minutes = timeForDeadline.minutes + (deadline.day * 24 * 60) - (nowTime.getDate() * 24 * 60)
                for (let i = nowTime.getMonth(); i < deadline.month; i++){
                    timeForDeadline.minutes = timeForDeadline.minutes + (array[i] * 24 * 60)
                }
                if(timeForDeadline.minutes >= 60){
                    timeForDeadline.hours = timeForDeadline.hours + Math.floor(timeForDeadline.minutes / 60)
                    timeForDeadline.minutes = timeForDeadline.minutes % 60
                }
                if(timeForDeadline.hours >= 24){
                    timeForDeadline.days = timeForDeadline.days + Math.floor(timeForDeadline.hours / 24)
                    timeForDeadline.hours = timeForDeadline.hours % 24
                }
                this.currentDeadline = timeForDeadline;
                this.deadlineClock(true, this.currentDeadline.license)
            }
        }
    }
</script>

<style scoped>
    .deadline-text {
        color: #d91c1d;
        font-size: 20px;
    }
    @media screen and (max-width: 500px){
        .deadline-text {
            font-size: 14px;
        }
    }
</style>